import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import {faBars}  from '@fortawesome/free-solid-svg-icons'

import './Navbar.css'


const styles = {
  
  background: "#000000",
  
};

function sayHello(x,y) {
x(y);
}

const Navbar = (props) => {
    return (

        <nav className="navbar navbar-expand-lg navbar-dark text-white" style={styles} >

<div className="container "> 

<button className="navbar-toggler" type="button" data-toggle="collapse"
data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
  <span className="navbar-toggler-icon"></span>
</button>


      
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ml-auto">
          

          <li className="nav-item">
              <a className="nav-link"  onClick={() => sayHello(props.Author,1)} > Home </a>
            </li>


            <li className="nav-item">
              <a className="nav-link"  onClick={() => sayHello(props.Author,2)} > Services </a>
            </li>

                
            <li className="nav-item">
              <a className="nav-link" onClick={() => sayHello(props.Author,3)}>Contact Me</a>
            </li>

                
     

            <li className="nav-item">
              <a className="nav-link" href="https://drive.google.com/file/d/1SLXhikdKZnQw0zIz-utEj1M7O5enmRvp/view?usp=sharing"> Resume </a>
            </li>

          </ul>
       
        </div>

        </div>

      </nav>

    )
}

export default Navbar

