import React, { Component } from "react";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Cards from "./Cards/Cards";
import Box from "@material-ui/core/Box";
import { makeStyles } from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";

const bstyles = {
  background: "#000000",
  color: "#FFFFFF",
};

const useStyles = makeStyles({
  cbox: {
    display: "block",
    justifyContent: "center",
    alignItems: "center",

    paddingTop: '100px'

  },
  cbox2: {
    display: "block",
    display: 'flex',
    flexDirection:'row',
    
    paddingTop: '100px',
    paddingLeft: '50px'

  }


});

function Center() {
  const matches = useMediaQuery("(max-width:700px)");
  const classes = useStyles();

  var content = <div></div>;

  if (matches == true) {
   

    content = (
      <div>
        
        <Box className={classes.cbox} style={bstyles}>
          <Cards
            Service="WEB DEVELOPER"
            Text="Full stack web development  "
            Text2="using MEAN / MERN"
          />
        </Box>

        <Box className={classes.cbox} style={bstyles}>
          <Cards Service="APP DEVELOPER " Text=" Python,C# " />
        </Box>

        <Box className={classes.cbox} style={bstyles}>
          <Cards
            Service="BLOCKCHAIN DEVELOPER"
            Text=" Blockchain Associate "
            Text2="and Ethereum Developer "
          />
        </Box>

      </div>
    );
  } else if (matches != true) {
  
    content = (
      <Box className={classes.cbox2} style={bstyles}>
        <Cards
          Service="WEB DEVELOPER"
          Text="Full stack web development  "
          Text2="using MEAN / MERN"
        />

        <Cards Service="APP DEVELOPER " Text=" Python,C# " />

        <Cards
          Service="BLOCKCHAIN DEVELOPER"
          Text=" Blockchain Associate "
          Text2="and Ethereum Developer "
        />
      </Box>
    );
  }

  return <div>{content}</div>;
}

export default Center;
